@import url(https://fonts.googleapis.com/css2?family=Bungee+Inline&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Open+Sans:ital,wght@0,300;0,400;0,700;0,800;1,300;1,400;1,600;1,700;1,800&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

* {
  padding: 0;
  margin: 0;
}

body {
  background-color: #014040;
}

.App {
  position: relative;
  text-align: center;
  color: #fff;
}

.App h1 {
  font-family: "Bungee Inline", cursive;
  font-size: 50px;
  margin-top: -5px;
  margin-bottom: -20px;
}

input {
  font-family: "Open Sans", sans-serif;
  font-size: 20px;
  padding-left: 10px;
  width: 550px;
  height: 50px;
  border: 5px solid #03a678;
  border-radius: 20px;
  outline: none;
  z-index: 200;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.search-container {
  text-align: left;
  font-family: "Open Sans", sans-serif;
  position: absolute;
  left: 50%;
  top: 168px;
  transform: translateX(-50%);
  background-color: #02735e;
  max-height: 300px;
  width: 535px;
  z-index: 199;
}

.search-container p:first-of-type {
  border-top: 3px solid #fff;
}

.search-container p {
  cursor: pointer;
  font-size: 18px;
  border-bottom: 3px solid #fff;
  padding: 10px 0 10px 10px;
}

.search-container p:hover {
  background-color: #18a88e;
}

.products {
  display: grid;
  grid-template-columns: repeat(4, 1fr);
  grid-gap: 25px;
  gap: 25px;
  margin: 100px 20px 20px 20px;
}

.word-container {
  font-family: "Open Sans", sans-serif;
  font-size: 15px;
  padding: 10px 0 10px 0;
  background-color: #5a8a81;
  border: 3px solid #fff;
}

.word-container h2 {
  font-weight: 800;
  letter-spacing: 0.7px;
  color: rgb(175, 248, 218);
  text-transform: capitalize;
  font-size: 22px;
}

.word-container h2::after {
  content: ":";
}

.word-container p {
  font-weight: 600;
  letter-spacing: 0.3px;
  padding: 5px 2px 10px 2px;
  border-bottom: 1px solid #fff;
}

.focusOut {
  position: absolute;
  top: 0;
  left: 0;
  background-color: #02735e00;
  height: 100vh;
  width: 100vw;
}

